}

summary {
  display: list-item;
}

.sprites-container {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  overflow: hidden;
}

.sprites {
  position: absolute;
  background-size: contain !important;
  background-repeat: no-repeat !important;
  animation-duration: 120s;
  animation-timing-function: linear;
}

.sprites svg {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
}

.sprite-rocket {
  position: relative;
  height: 120px;
  width: 68px;
  top: 30%;
  left: -20%;
  transform: rotate(110deg);
  animation-name: rocket-path;
  animation-timing-function: linear;
  z-index: -10;
}

.rocket-fire {
  position: absolute;
  height: 100%;
  width: 100%;
  top: 80%;
  left: 2px;
  transform: rotate(90deg) scale(2);
  z-index: -1;
  animation-name: rocket-fire-path;
  animation-duration: 60s;
  animation-timing-function: step-end;
  transition: unset;
}

.sprite-enemy {
  position: relative;
  height: 120px;
  width: 88px;
  margin: 0;
  top: 10%;
  left: -20%;
  transform: rotatey(180deg);
  animation-name: enemy-path;
  animation-timing-function: linear;
  z-index: 0;
}

.sprite-enemy-laser {
  position: absolute;
  display: block;
  height: 2rem;
  width: 4rem;
  box-shadow: 0 0 13px 0 white;
  opacity: 0;
  border-radius: 50% 10% 10% 50%;
  background-image: linear-gradient(to right top, #ffa17a, #ff7d47, #ffc5ad);
  animation-name: enemy-laser;
  animation-duration: 120s;
  animation-timing-function: linear;
}

.sprite-boom {
  position: absolute;
  height: 500%;
  width: 500%;
  top: -220%;
  left: -200%;
}
.sprite-boom svg {
  fill: #ffa17a;
  stroke: #000;
  stroke-miterlimit: 7;
  stroke-width: 4;
  opacity: 0;
  animation-name: boom-path;
  animation-duration: 30s;
  animation-timing-function: step-end;
}

@keyframes rocket-path {
  0% {
    top: 30%;
    left: -75%;
  }
  2% {
    top: 30%;
    left: 20%;
    transform: rotate(95deg);
  }
  3% {
    transform: rotate(110deg);
  }
  4% {
    top: 55%;
    left: 45%;
    transform: rotate(50deg);
  }
  5% {
    top: 25%;
    left: 58%;
    transform: rotate(100deg);
  }
  6% {
    top: 35%;
    left: 195%;
    transform: rotate(80deg);
  }
  7% {
    left: 115%;
    transform: rotate(-100deg);
  }
  8% {
    top: 55%;
    left: -110%;
    transform: rotate(-100deg);
  }
  9% {
    top: 10%;
    left: -25%;
    transform: rotate(100deg);
  }
  10% {
    left: 185%;
    top: 35%;
    transform: rotate(100deg);
  }
  11% {
    transform: rotate(-100deg);
    left: 110%;
    animation-timing-function: cubic-bezier(0.68, -0.55, 0.265, 1.55);
  }
  13% {
    transform: rotate(-100deg);
    top: 10%;
    left: 80%;
    transform: rotate(0deg);
    animation-timing-function: cubic-bezier(0.48, -0.25, 0.25, 1);
  }
  14% {
    top: 15%;
    left: 82%;
    filter: none;
    animation-timing-function: step-end;
  }
  15% {
    top: 13%;
    left: 82%;
    transform: rotate(0deg);
    animation-timing-function: step-end;
  }
  16% {
    top: 13%;
    left: 82%;
    filter: sepia(10);
    transform: rotate(0deg);
    animation-timing-function: linear;
  }
  23% {
    top: 90%;
    left: 90%;
    transform: rotate(165deg) scale(0);
  }
  100% {
    top: 70%;
    left: 90%;
    transform: rotate(165deg) scale(0);
  }
}
@keyframes rocket-fire-path {
  0% {
    top: 90%;
    opacity: 1;
    transform: rotate(90deg) scale(2);
  }
  4% {
    top: 60%;
    opacity: 0;
    transform: rotate(90deg) scale(1);
  }
  8% {
    opacity: 1;
  }
  9% {
    opacity: 0;
  }
  10% {
    top: 90%;
    opacity: 1;
    transform: rotate(90deg) scale(2.2);
  }
  12% {
    top: 120%;
    transform: rotate(90deg) scale(3);
  }
  22% {
    top: 50%;
    transform: rotate(90deg) scale(1);
  }
