    opacity: 1;
    transform: rotatex(1080deg);
    animation-timing-function: linear;
  }
  15% {
    top: 22%;
    left: 80%;
    transform: rotatex(6020deg);
    animation-timing-function: step-start;
    opacity: 1;
  }
  16% {
    opacity: 0;
    animation-timing-function: step-start;
  }
  100% {
    opacity: 0;
  }
}
@keyframes boom-path {
  0% {
    opacity: 0;
  }
  60% {
    opacity: 1;
    transform: rotate(12deg) scale(1.2);
  }
  61% {
    transform: rotate(-12deg) scale(1);
  }
  62% {
    transform: rotate(12deg) scale(1.2);
  }
  64% {
    transform: rotate(-12deg) scale(1);
    animation-timing-function: linear;
    opacity: 1;
  }
  65% {
    opacity: 0;
  }
}
@keyframes enemy-surprise {
  0% {
    opacity: 0;
  }
  6% {
    opacity: 1;
    animation-timing-function: step-start;
  }
  7% {
    opacity: 0;
    animation-timing-function: linear;
  }
}